import React, { useLayoutEffect } from 'react';
import { Link } from 'react-router-dom';
import { Brand } from '../../components';
import './about.css';

const About = () => {
    useLayoutEffect(() => {
        window.scrollTo(0, 0)
    });

  return (
    <div className="about__container header__padding">
        <div className="about__container-content">
            <h1>Vi jobbar med städ och fastighetsskötsel (Ej klar)</h1>
            <div className="about__container-content-body">
                <div className="about__container-content-body_title">
                    <div className="about__container-content-body_marker"/>
                    <p>Introduktion</p>
                </div>
                <div className="about__container-content-body_text">
                    <p>Välkommen till oss på Städenergi AB. Vi är ett Stockholmsbaserat innovativt städföretag som utför tjänster inom städ och fastighetsskötsel hos företag, banker, institutioner och i privata hem.</p>
                    <p>Geografiskt jobbar vi i Stockholms län och dess närområde. Kontoret finns i Årsta på Byängsgränd 6. Verksamheten startade 2010 och sysselsätter idag cirka 60 anställda.</p>
                    <p>Vi har kollektivavtal genom Almega Serviceentreprenad och är ett auktoriserat serviceföretag. Städenergi Ansvarsförsäkring finns tecknat hos IF-försäkring.</p>   
                </div>    
            </div>
        </div>
        <Brand />
        <div className="about__container-content">
            <div className="about__container-content-body">
                <div className="about__container-content-body_title">
                    <div className="about__container-content-body_marker"/>
                    <p>Vad skiljer oss från andra?</p>
                </div>
                <div className="about__container-content-body_text">
                    <p>Alltför ofta försöker konkurrenter i vår bransch att lyckas genom att helt enkelt erbjuda de billigaste lösningarna och det brukar vanligtvis avspeglas i städningen. Vi erbjuder löner och förmåner över branschens genomsnitt, och lockar därigenom fram produktiva människor som tillhandahåller utomordentlig service. På så sätt lockar vi fram det bästa resultatet.</p>
                    <p>Vi tillämpar de senaste rengöringsteknikerna enligt den typ av städservice som våra kunder efterfrågar. Dessutom använder vi professionell rengöringsutrustning och rengöringsmedel som är miljövänliga och av hög kvalitet! Alla dessa faktorer, tillsammans med alla våra skickliga och utbildade medarbetare inom detta område gör oss till ett av de mest pålitliga namnen när det gäller städtjänster i Stockholm.</p>
                    <p>Vi förser våra anställda med rätt utbildning, korrekt utrustning och förnödenheter som de behöver för att utföra sina uppgifter på det mest skickliga sätt som möjligt. Detta innebär också att behandla våra anställda med respekt och uppskattning. Genom att skapa en positiv arbetsmiljö är våra medarbetare naturligtvis mer benägna att prestera på toppnivå.</p>   
                </div>    
            </div>
        </div>
        <div className="about__container-buttons">
            <Link to="/Offertforfragan"><input type="button" value="Gör en offertförfrågan" /></Link>
            <Link to="/Tjanster"><input type="button" value="Läs mer om våra tjänster" /></Link>
        </div>
    </div>
  )
}

export default About