import React from 'react';
import { Link } from 'react-router-dom';

const Success = () => {
    return (
        <div className="success__container">
            <h1>Tack för din offertförfrågan!</h1>
            <p style={{maxWidth: "600px"}}>Inom kort skickar vi tillbaka en offert! Om du har några frågor går det bra att maila oss på kontakt@mzstädservicenacka.se</p>
            <div>
                <Link to="/"><button type="button" id="prevButton">Till startsidan</button></Link>
                <Link to="/Om-Stadservice-Nacka"><button type="button" id="prevButton">Läs mer om oss</button></Link>
            </div>
        </div>
    )
}

export default Success